// @use 'partials/mixins' as *;


@font-face {
  font-family: "AvenirNextLTPro-Regular";
  src: url("../assets/font/AvenirNextLTPro-Regular.otf") format("opentype");
}

@font-face {
  font-family: "AvenirNextLTPro-Demi";
  font-weight: 600;
  src: url("../assets/font/AvenirNextLTPro-Demi.otf") format("opentype");
}

@font-face {
  font-family: "AvenirNextLTPro-Bold";
  font-weight: 700;
  src: url("../assets/font/AvenirNextLTPro-Bold.otf") format("opentype");
}

$tablet-width: 768px;
$desktop-width: 1200px;

@mixin tablet {
  @media (min-width: $tablet-width) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: $desktop-width) {
    @content;
  }
}



$pri-dark: #323232;
$pri-mid: #afafaf;
$pri-light: #ffffff;

$sec-dark: #000000;
$sec-mid: #e1e1e1;
$sec-light: #fafafa;

// Text calculations

// Mobile Text Styles

// Page header
// 24px, h1(?), = 1.5
// LH 32px / 2

// Section header
// 24px, h2(?) = 1.5
// LH 32px / 2

// Subheader
// 14px, h3(?) = 
// LH 22px / 0.875

// Body copy
// 13px, h4(?) = 0.813
// LH 18px / 1.125

// Footer copy
// 13px, p(?) = .813
// LH 18px / 1.125

// Labels & Buttons
// 10px, p(styled?) = 0.625
// LH 20px / 1.25

// Desktop & Tablet Text Style

// Page header
// 60px, h1(?) = 3.75
// LH 68px / 4.25

// Section header
// 36px, h2(?) = 2.25
// LH 44px / 2.75


// Subheader
// 24px, h3(?) = 1.5
// LH 32px / 2

// Body copy
// 14px, h4(?) = 1.143
// LH 22px / 1.375

// Footer copy
// 14px, p(?) = 0.875
// LH 20px, 1.25

// Labels & Buttons
// 12px, p(styled?) = 0.75
// LH 20px, 1.25

// Padding calculations

// 80 = 5
// 64 = 4
// 56 = 3.5
// 48 = 3
// 40 = 2.5
// 32 = 2
// 24 = 1.5
// 16 = 1
// 8 = 0.5
// 4 = 0.25

//

img {
  max-width: 100%;
}

body {
  font-family: "AvenirNextLTPro-Regular";
  padding: 0;
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

h2 {
  color: $sec-dark;
}

.body__header {
  width: 100%;
  background-color: $pri-dark;
  display: flex;
  align-items: center;

  @include desktop() {
    width: auto;
    align-content: space-between;
    flex-direction: row;
  }

  #navbar-logo {
    order: -1;
    width: 100%;
    color: $pri-light;
    font-size: 1.5rem;
    text-align: center;
    padding: 1rem;
    margin-right: auto;
    text-transform: uppercase;
    padding-top: 2rem;
    padding-bottom: 0;

    @include desktop() {
      padding-top: 1rem;
      padding-bottom: 1rem;
    }
  }

  #navbar-active {
    border-style: solid;
    border-width: 0;
    border-bottom-width: 0.25rem;
    border-color: $pri-light;
  }

  nav {
    width: 100%;

    @include desktop() {
      width: 30%;
    }
  }

  ul {
    list-style-type: none;
    // width: 50%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;

    @include desktop() {

      flex-wrap: unset;
    }
  }

  ul li {
    width: 50%;
    box-sizing: border-box;
    padding: 1rem;
  }

  ul li a {
    color: $pri-light;
    text-decoration: none;
  }
}

footer {
  background-color: $pri-dark;
  box-sizing: border-box;
  padding: 1rem;
  color: $pri-light;

  img {
    margin: 1rem;
  }

  ul {
    list-style-type: none;
    display: flex;
    flex-direction: column;

    @include desktop {
      list-style-type: none;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-around;

      .ul__full-width {
        width: 100%;
      }
    }

    .ul__social {
      @include desktop {

        display: flex;
        justify-content: flex-start;
        flex-direction: row;
        padding-left: 6rem;

        p {
          flex-grow: 2;
        }

        div {
          flex-grow: 4;
          margin-left: 0.5rem;
        }

      }
    }
  }

  .li__bold {
    font-weight: bold;
  }
}




